import React from "react"
import { graphql } from "gatsby"
import PropTypes from 'prop-types'
import About from "../components/about.jsx"
import Plot from "../components/plot"
import Banner from "../components/banner"
import Characters from "../components/characters"
import Screens from "../components/screens"
import Header from "../components/header"
import Footer from "../components/footer"


const IndexPage = ({ data }) => {
  const serializedData = data.markdownRemark.frontmatter
  return (
    <div>
      <div className="bg"></div>
      <Header data={serializedData.headerData} />
      <Banner data={serializedData.bannerData} />
      <About data={serializedData.aboutData} />
      <Plot data={serializedData.plotData} />
      <Characters data={serializedData.charactersData} />
      <Screens data={serializedData.screensData} />
      <Footer data={serializedData.footerData} />
    </div>
  )
}

IndexPage.propTypes = {
  data: PropTypes.shape({
    markdownRemark: PropTypes.shape({
      frontmatter: PropTypes.object,
    }),
  }),
}

export default IndexPage

export const pageQuery = graphql`
  query IndexPage {
     markdownRemark(frontmatter: { templateKey: { eq: "index-page" } }) {
      frontmatter {
        headerData {
          nav {
            text
            link
          }
          soundImage
          currentLocale
          locales {
            text
            link
          }
        }
        bannerData {
          image
        }
        aboutData {
            title
            description
            image
          }
        plotData {
            title
            description
        }
        charactersData {
          title
          characters {
            title
            image
            subtitle
            description
          }
        }
        screensData {
          title
          screens {
           image
          }
        }
        footerData {
          title
          images {
            alt
            src
          }
        }
      }
    }
  }
`